html,
body {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.app-container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-color: #f7f7fa;
}
.middle-container {
  margin-left: 75px;
  width: calc(100vw - 75px);
  transition: 0.3s;
}
.middle-container-shrink {
  margin-left: 230px;
  width: calc(100vw - 230px);
  transition: 0.3s;
}

.main-container {
  height: 100vh;
  overflow: hidden;
}

.lazy-load{
  height: 100vh;
}
