.login-design-wrapper {
  height: 100%;
  background-image: url(../../../../assets/img/login-design-bg.png);
  background-size: cover;
  border-radius: 10px;
  padding: 20px;
  background-position: bottom;
  position: relative;
  display: flex;
}
.logo-wrapper {
  width: 140px;
  position: absolute;
}
.logo-wrapper svg {
  width: 100%;
}
.login-design-title {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 42px;
  margin: auto 16px;
}
