.otp-inputs-container {
  display: flex;
  justify-content: space-between;
  input {
    background-color: #f4f4fc;
    text-align: center;
    height: 46px;
    width: 56px;
  }
  input:focus {
    background-color: #f4f4fc;
    text-align: center;
    box-shadow: none;
    border: 1px solid #dee2e6 !important;
  }
}

.error-boundary {
  .otp-inputs-container.invalid {
    input {
      border: 1px solid #eb5e5e !important;
    }
  }
}
