.login-page-container {
  .login-input-wrapper-container {
    height: 100vh;
    padding: 20px;
    display: grid;
    overflow-y: auto;
    overflow-x: hidden;
    .login-input-wrapper {
      margin: auto;
      width: 100%;
      max-width: 487px;
      .welcome-text {
        color: var(--primary-color);
        margin-bottom: 10px;
        font-weight: 600;
        font-size: 30px;
      }
      h3 {
        font-weight: 900;
        font-size: 36px;
      }
      .description {
        color: var(--text-color-light);
        font-size: 14px;
      }
      .error-info {
        font-size: 13px;
        color: #0a1629;
      }
    }
  }

  /* width */
  .login-input-wrapper-container::-webkit-scrollbar {
    width: 0px;
  }

  /* Track */
  .login-input-wrapper-container::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  .login-input-wrapper-container::-webkit-scrollbar-thumb {
    background: #d6d6d6;
  }

  // form customized
  .form-label {
    margin-bottom: 0px;
  }
}

@media screen and (max-width: 768px) {
}
