.registration-page-container {
  .registration-input-wrapper-container {
    height: 100vh;
    padding: 20px;
    display: grid;
    overflow-y: auto;
    overflow-x: hidden;
    .registration-input-wrapper {
      margin: auto;
      width: 100%;
      max-width: 487px;
      .infoNote {
        color: #abb2b3;
        font-size: 15px;
        font-weight: 500;
        display: flex;
        gap: 8px;
      }
      .custom-mobile-input {
        display: flex;
        justify-content: space-between;
        .mb-country {
          width: 25%;
          padding: 1px;
          bottom: 0px;
        }
        .mb-input {
          width: 70%;
        }
      }
    }
  }

  /* width */
  .registration-input-wrapper-container::-webkit-scrollbar {
    width: 0px;
  }

  /* Track */
  .registration-input-wrapper-container::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  .registration-input-wrapper-container::-webkit-scrollbar-thumb {
    background: #d6d6d6;
  }

  .stepper-progress-bar {
    width: 487px;
    margin-bottom: 44px;
    .stepper-icon {
      width: 444px;
      display: flex;
      gap: 24px;
      align-items: center;

      .stepper-icon-container {
        width: 44px;
        .icon {
          width: 44px;
          height: 44px;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .title {
          font-weight: bold;
          white-space: nowrap;
        }
      }

      .stepper-icon-container.active {
        .icon {
          border: 2px solid #7d63ec;
          background-color: #7d63ec;
        }
        .title {
          color: #7d63ec;
        }
      }
      .stepper-icon-container.filled {
        .icon {
          border: 2px solid #009896;
          background-color: #009896;
        }
        .title {
          color: #009896;
        }
      }
      .stepper-icon-container.blank {
        .icon {
          border: 2px solid #f5f5f5;
        }
        .title {
          color: #888888;
        }
      }

      .line {
        width: 108px;
        border-bottom: 1px solid #e1e1e1;
        margin-bottom: 16px;
      }
    }
  }

  .otp-title {
    font-size: 24px;
    color: black;
    font-weight: 900;
  }

  // form customized
  .form-label {
    margin-bottom: 0px;
  }

  .custom-label {
    font-size: 13px;
    color: #888888;
    font-weight: 500;
  }
}

.termAndCondition {
  .title {
    font-size: 16;
    font-weight: bold;
    color: #1c1d21;
  }
  .description {
    color: #323232;
    font-size: 14px;
  }
}

@media screen and (max-width: 768px) {
  .registration-page-container {
    overflow: auto;
    height: 100vh;
    .registration-input-wrapper-container {
      overflow: auto;
      height: initial;
    }
    .line {
      width: 70px;
    }
  }
}
