/*custom offcanvas */

.offcanvas-header {
  border-bottom: 1px solid #eef5f6;
}

.offcanvas-title {
  font-size: 18px !important;
  font-weight: 700 !important;
  line-height: 30px !important;
  color: #5a557a !important;
}

.offcanvas-custom-body {
  padding: 0px !important;
  overflow-y: hidden !important;
  position: relative;

  .offcanvas-body-content {
    width: 100% !important;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 70px;
    overflow-y: auto;
  }

  .offcanvas-footer-content {
    background-color: #f5f5fa;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 70px;
    padding: 16px;
    div[role="button"] {
      font-size: 15px;
    }
  }
}

.error-boundary.error {
  .form-label {
    color: #eb5e5e;
  }
  .input-one-line {
    border-bottom: 1px solid #eb5e5e !important;
  }
  .input-error-text {
    color: #eb5e5e;
    font-size: 13px;
    padding-bottom: 8px;
  }
  .custom-dropdown {
    border-color: #eb5e5e !important;
  }
}

.readonly-form {
  .input-one-line {
    border-color: transparent !important;
  }
  .custom-dropdown {
    border-color: transparent !important;
  }
  .custom-dropdown-multi {
    border-color: transparent !important;
  }
  .e-ddl.e-input-group.e-control-wrapper .e-ddl-icon {
    display: none;
  }
  .e-multiselect.e-input-group .e-ddl-icon::before {
    display: none;
  }
  .customDateTimePicker {
    .e-input-group {
      border-color: transparent !important;
    }
    .e-icons {
      display: none !important;
    }
  }
  .CustomDatePicker {
    border: transparent;
    .ant-picker-suffix {
      display: none !important;
    }
  }
  .custom-time-select {
    border-color: transparent !important;
    &.ant-picker-disabled {
      &:hover {
        background-color: #ffffff !important;
      }
      background-color: #ffffff !important;
      cursor: auto !important;
      input[disabled] {
        cursor: auto !important;
      }
      .ant-picker-suffix {
        display: none !important;
      }
    }
  }
}

/* custom pagination style  */

.e-grid .e-gridpager {
  font-family: "Lato";
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  gap: 6px;
  font-size: 14px;
}

.e-pager .e-icons.e-prev:before {
  content: "PREV" !important;
}

.e-pager .e-icons.e-next:before {
  content: "NEXT" !important;
}

.e-pager .e-icon-last::before {
  content: "\ec07" !important;
}

.e-pager .e-icon-first::before {
  content: "\e904" !important;
}

.e-grid .e-gridpager .e-nextpage {
  content: "\e913" !important;
}

.e-pager .e-icon-prev::before,
.e-icons.e-next:before {
  font-size: 14px;
  font-family: "Lato";
  color: #8181a5;
  font-weight: bolder;
}

.e-grid .e-gridpager .e-numericitem {
  background: #fff;
  border-right-color: #e0e0e0;
  color: #000;
  border-radius: 6px;
  padding: 10px 15px;
  font-weight: bolder;
}

.e-pager .e-currentitem,
.e-grid .e-gridpager .e-numericitem:hover,
.e-pager .e-icon-prev:hover {
  background: rgba(94, 129, 244, 0.1) !important;
  color: #7d63ec !important;
  background: #fff;
  border-right-color: #e0e0e0;
  color: #000;
  border-radius: 6px;
  padding: 10px 15px;
  transition: 0.2s all;
}

.custom-nav-tabs {
  display: flex;
  gap: 20px;
  .nav-link {
    font-size: 14px;
    border: none !important;
    color: #888888;
    padding: 0;
    padding-bottom: 6px;
    &:hover {
      color: #005a5a !important;
    }
    transition: border-bottom 100ms linear;
  }
  .nav-link:hover {
    text-decoration: none !important;
    border-bottom: 3px solid #c4c4c4 !important;
  }
  .nav-link.active {
    border-bottom: 3px solid #00aeab !important;
    color: #005a5a !important;
    font-weight: 600 !important;
    &:focus,
    &:focus-visible {
      background: none !important;
      color: #005a5a !important;
      box-shadow: none !important;
    }
  }
}

.simple-table {
  &.custom-hover {
    tr:hover td {
      background-color: #eeeeee !important;
      cursor: pointer;
    }
  }
  .border-right-2px {
    border-right: 2px solid #e4e5eb;
  }
  thead.bold-header tr th {
    font-weight: bold;
    text-transform: uppercase;
    font-weight: 800;
  }

  thead tr {
    border-radius: 4px;
    height: 50px;
    th {
      word-spacing: 3px;
      text-transform: uppercase;
      vertical-align: middle;
      white-space: nowrap;
      border-bottom: 1px solid #d4cfff !important;
      background: #f9f9ff;
      color: #1c1d21;
      font-size: 14px;
    }
  }
  tbody tr td {
    color: #525361;
    font-weight: 500;
    font-size: 15px;
    border-bottom: 1px solid #e4e5eb;

    .no-data-border-red {
      border: 1px solid #ff0000;
      border-radius: 6px;
    }
  }
}

